.flexContainer {
    display: -ms-flexbox;
    display: flex;
}
.flexItem {
    -ms-flex: 1 1;
        flex: 1 1;
}
.flexStart {
    -ms-flex-pack: start;
        justify-content: flex-start;
}
.flexCenter {
    -ms-flex-pack: center;
        justify-content: center;
}
.flexEnd {
    -ms-flex-pack: end;
        justify-content: flex-end;
}
.flexWrap {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.itemCenter {
    -ms-flex-align: center;
        align-items: center;
}
.flexColumn {
    -ms-flex-direction: column;
        flex-direction: column;
}
.flexSpaceBetween {
    -ms-flex-pack: justify;
        justify-content: space-between;
}
.flexSpaceAround {
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.nav {
    list-style: none;
}
.nav a {
    text-decoration: none;
    display: block;
    padding: 0 1em;
    color: black;
}




